										that.$api.prePage().refresh();
									}
									if (prePage.route == 'pages/cart/cart') {

										that.$api.prePage().refresh();
									}
									uni.navigateBack();
								} else {


									that.$api.msg(res.data.info);
									that.logining = false;

								}



							}
						});



					} else {


						that.$api.msg(res.data.info);
						that.logining = false;

					}



